<template>
    <div>
        <div class="navbar">
            <div @click="changeTab('home')" class="navbar__tab" :class="currentTab === 'home' ? 'navbar__tab--active' : ''">Add User Details</div>
            <div @click="changeTab('about')" class="navbar__tab" :class="currentTab === 'about' ? 'navbar__tab--active' : ''">List User Details</div>
        </div>        
        <VueAddUser v-if="currentTab === 'home'" @passUserData="handleUserData"/>        
        <VueListUser v-else-if="currentTab === 'about'" :userData="userData"/>
        <div v-else>
            <h1>This is link page</h1>
        </div>        
    </div>
</template>

<script setup lang="ts">
import {ref} from 'vue'

import VueAddUser from './Tabs/VeutifyTabs/VueAddUser.vue'
import VueListUser from './Tabs/VeutifyTabs/VueListUser.vue'
 
export interface UserData {
    name: string,
    age: number | null,
    email: string,
}   

const currentTab = ref<string>('home');
const userData = ref<UserData[]>([]);

const changeTab = (tab:string) => {
    currentTab.value = tab;
}

const handleUserData = (userDetails:UserData) => {
    userData.value.push(userDetails);
}
</script>

