<template>
    <div class="container">
        <v-form ref="addUserForm" v-model="isFormValid" class="container__form">
            <v-text-field 
            v-model="users.name.value" 
            :rules="[rules.required]" 
            label="First name" 
            :maxlength="15"
            @keypress="handleNameInput" 
            class="container__form__input"
            />
            <v-text-field 
            v-model="users.age.value"                
            class="container__form__input container__form__input--appearance-none" 
            :rules="[rules.required, rules.isAgeLesser]"                
            label="Age" 
            type="number"
            @keypress="handleAgeInput"
            />
            <v-text-field
            v-model="users.email.value" 
            :rules="[rules.required, rules.email]" 
            label="E-mail" 
            :maxlength="20" 
            class="container__form__input"
            />
            <v-btn 
            :disabled="!isFormValid" 
            @click="validate({name: users.name.value, age: users.age.value, email: users.email.value})" 
            color="success" 
            class="container__form__button"
            >
                Submit
            </v-btn>
        </v-form>
    </div>
</template>

<script setup lang="ts">
import { ref, defineEmits } from 'vue';

import {UserData} from '../../TabsPage.vue'

interface UserDetails {
    value : string,
    isError : boolean,
}

interface Users {
    name: UserDetails,
    age: {
        value: number | null,
        isError: boolean,
    },
    email: UserDetails,
}

const emit = defineEmits(['passUserData'])
const isFormValid = ref<boolean>(true);
const addUserForm = ref<HTMLFormElement>();
const restrictedAgeInputCharacters = ['e', '.', '-', '+'];
const rules = {
    required: (value: string) => !!value || 'Required',
    isAgeLesser: (value: number) => value >= 18 || 'Age is less',
    email: (value: string) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

        return pattern.test(value) || 'Invalid e-mail'
    }
}
const users = ref<Users>({
    name: {
        value: '',
        isError: false,        
    },
    age: {
        value: null,
        isError: false,
    },
    email: {
        value: '',
        isError: false,        
    },
});

const handleNameInput = (event: KeyboardEvent) => {
    const nameInputValue = event?.key;
    const regEx = /^[a-zA-Z]+$/

    if (!regEx.test(nameInputValue) && nameInputValue !== ' ') {
        event.preventDefault();
    }
}

const handleAgeInput = (event: KeyboardEvent) => {
    const ageInputValue = event.key;
    const isGreaterLength = users.value.age.value !== null && String(users.value.age.value).length > 2;

    if (restrictedAgeInputCharacters.includes(ageInputValue) || isGreaterLength) {
        event.preventDefault();
    }
}

const validate = (userDetails:UserData) => {
    emit('passUserData', userDetails)
    addUserForm.value?.reset()
}
</script>

<style scoped lang="scss">
.container {
    display: flex;
    justify-content: center;

    &__form {
        width: 300px;
        background-color: lightblue;
        padding: 20px 30px;
        border-radius: 5px;
        font-size: 2rem;

        &__input {
            font-size: 14px;

            &--appearance-none::-webkit-inner-spin-button {
                -webkit-appearance: none;            
            }
        }

        &__button {
            background-color: green !important;
        }
    }
}
</style>